.our-story-section {
  display: flex;
  flex-direction: column;
  margin: 1em;
}

.img-story {
  width: 100%;
  height: auto;
  margin-top: 7em;
}

.right-column {
  margin-top: 2em;
}

.story-h3 {
  font-size: 1.4rem;
  text-align: center;
}

.p-story {
  width: 100%;
  margin-left: auto;
  margin-right: auto;
}

.our-clothing {
  margin-top: 3em;
  display: flex;
  flex-direction: column;
  margin-bottom: 4em;
}

.photos-row {
  display: flex;
  flex-direction: column;
}
.photos-row img {
  margin-left: auto;
  margin-right: auto;
  width: 90%;
  margin-bottom: 2em;
  height: auto;
}

.first-column {
  margin-top: 3em;
  height: 95vh;
}
.first-column h3 {
  font-size: 1.4rem;
  text-align: center;
}
.first-column p {
  width: 90%;
  margin-left: auto;
  margin-right: auto;
}

@media screen and (min-width: 800px) {
  .our-story-section {
    display: flex;
    margin: 1em;
    flex-direction: row;
  }
  .img-story {
    width: 90%;
    height: auto;
    margin-top: 6em;
    margin-left: 2em;
  }
  .right-column {
    margin-top: 10em;
  }
  .story-h3 {
    text-align: center;
  }
  .p-story {
    width: 75%;
    margin-left: auto;
    margin-right: auto;
  }
  .our-clothing {
    margin-top: 8em;
    display: flex;
    flex-direction: row;
  }
  .photos-row {
    display: flex;
    flex-direction: column;
  }
  .photos-row img {
    width: 90%;
    margin-bottom: 2em;
    height: auto;
  }
  .first-column {
    margin-top: 3em;
    height: 95vh;
  }
  .first-column h3 {
    text-align: center;
  }
  .first-column p {
    width: 75%;
    margin-left: auto;
    margin-right: auto;
  }
}